* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.nav-list {
    height: 10vh;
    display: flex;
    width: 50%;
    justify-content: space-around;
    align-items: center;
    margin-left: auto;
    list-style: none;
}

.nav-links {
    text-decoration: none;
    color: black;
    font-size: 18px;
}

.home,
.about,
.contact {
    height: 90vh;
    position: absolute;
    width: 100%;
}

.home-content,
.about-content,
.contact-content {
    height: 100%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;

    img {
        height: 70%;
    }

    h1 {
        font-size: 50px;
        margin: 30px 0;
    }

    p {
        font-size: 20px;
    }
}

.home {
    background-image: linear-gradient(to top, #a18cd1 0%, #fbc2eb 100%);
}

.about {
    background-image: linear-gradient(to top, #d299c2 0%, #fef9d7 100%);
}

.contact {
    background-image: linear-gradient(to top, #fdcbf1 0%, #fdcbf1 1%, #e6dee9 100%);
}